import GameObject from '../object/GameObject';
export default class UI {
    static createUIComponent(params) {
        if (!params) {
            return;
        }
        params = Object.assign({}, UI.DEFAULT_PARAMS, params);
        return GameObject.create(params);
    }
    static createButton(params) {
    }
}
UI.DEFAULT_PARAMS = {
    'fixedPosition': true,
    components: {
        collider: {},
        spriterenderer: {},
    }
};
